/*BODY*/
body {
  font-size: 20px;
  font-weight: 400;
  font-family: "Inter", sans-serif;
  line-height: 140%;
  color: #101010;
  margin: 0px;
}

.dark {
  background-color: #1E1E1E;
  color: #fff;
}

@media (max-width: 680px) {
  body {
    font-size: 16px;
    line-height: 140%;
  }
}
/*CONTENER*/
.contener {
  max-width: 680px;
  margin: 0 auto;
  padding: 0px 16px;
}

/*LINK for white BG*/
.cs-link {
  color: #2563EB;
  text-decoration: none;
}
.cs-link:hover {
  text-decoration: underline;
}

/*TITLES*/
.case-title {
  font-size: 28px;
  font-weight: 500;
  margin: 0px 0px 30px 0px;
}

@media (max-width: 680px) {
  .case-title {
    font-size: 20px;
    margin: 0px 0px 24px 0px;
  }
}
.case__wrapper80 {
  padding: 80px 0px;
}

.list__item {
  display: flex;
  gap: 30px;
}
.list__item .akapit {
  display: block;
  margin-bottom: 8px;
}
.list__item .list__number {
  font-size: 16px;
}
@media (max-width: 680px) {
  .list__item .list__number {
    font-size: 14px;
  }
}
.list__item .list__text--bottom {
  border-left: 2px solid rgba(17, 0, 0, 0.08);
  color: #7B7B7C;
  padding-left: 12px;
  display: flex;
  flex-direction: column;
  gap: 8px;
  margin-top: 12px;
}

@media (max-width: 680px) {
  .list__item {
    gap: 16px;
  }
}
.header {
  width: 100%;
  padding: 40px 0px 80px 0px;
  position: sticky;
  top: 0;
  z-index: 1000;
  font-size: 16px;
}

.header__nav-wrapper {
  display: flex;
  gap: 16px;
}
.header__nav-wrapper .button__nav {
  background-color: rgba(255, 255, 255, 0.8);
  -webkit-backdrop-filter: blur(3px);
          backdrop-filter: blur(3px);
  border-radius: 8px;
  border: 1.3px solid rgba(15, 27, 94, 0.1);
  font-weight: 500;
}
.header__nav-wrapper .button__nav--link {
  color: #101010;
  text-decoration: none;
  display: block;
  padding: 4px 12px;
}
.header__nav-wrapper .button__nav--dark {
  background-color: #1E1E1E;
  border: 1.3px solid rgba(255, 255, 255, 0.1);
  color: #fff;
  font-weight: 400;
}
.header__nav-wrapper .button__nav--dark .button__nav--link {
  color: #fff;
}

.hero {
  padding-bottom: 80px;
}
.hero .hero__wrapper {
  padding-bottom: 80px;
  display: flex;
  flex-direction: column;
  gap: 16px;
}
.hero .hero__wrapper .case-title {
  margin-bottom: 16px;
}

@media (max-width: 680px) {
  .contener {
    height: 90%;
  }
}