body {
  color: #101010;
  margin: 0;
  font-family: Inter, sans-serif;
  font-size: 20px;
  font-weight: 400;
  line-height: 140%;
}

.dark {
  color: #fff;
  background-color: #1e1e1e;
}

@media (width <= 680px) {
  body {
    font-size: 16px;
    line-height: 140%;
  }
}

.contener {
  max-width: 680px;
  margin: 0 auto;
  padding: 0 16px;
}

.cs-link {
  color: #2563eb;
  text-decoration: none;
}

.cs-link:hover {
  text-decoration: underline;
}

.case-title {
  margin: 0 0 30px;
  font-size: 28px;
  font-weight: 500;
}

@media (width <= 680px) {
  .case-title {
    margin: 0 0 24px;
    font-size: 20px;
  }
}

.case__wrapper80 {
  padding: 80px 0;
}

.list__item {
  gap: 30px;
  display: flex;
}

.list__item .akapit {
  margin-bottom: 8px;
  display: block;
}

.list__item .list__number {
  font-size: 16px;
}

@media (width <= 680px) {
  .list__item .list__number {
    font-size: 14px;
  }
}

.list__item .list__text--bottom {
  color: #7b7b7c;
  border-left: 2px solid #11000014;
  flex-direction: column;
  gap: 8px;
  margin-top: 12px;
  padding-left: 12px;
  display: flex;
}

@media (width <= 680px) {
  .list__item {
    gap: 16px;
  }
}

.header {
  width: 100%;
  z-index: 1000;
  padding: 40px 0 80px;
  font-size: 16px;
  position: sticky;
  top: 0;
}

.header__nav-wrapper {
  gap: 16px;
  display: flex;
}

.header__nav-wrapper .button__nav {
  -webkit-backdrop-filter: blur(3px);
  -webkit-backdrop-filter: blur(3px);
  backdrop-filter: blur(3px);
  background-color: #fffc;
  border: 1.3px solid #0f1b5e1a;
  border-radius: 8px;
  font-weight: 500;
}

.header__nav-wrapper .button__nav--link {
  color: #101010;
  padding: 4px 12px;
  text-decoration: none;
  display: block;
}

.header__nav-wrapper .button__nav--dark {
  color: #fff;
  background-color: #1e1e1e;
  border: 1.3px solid #ffffff1a;
  font-weight: 400;
}

.header__nav-wrapper .button__nav--dark .button__nav--link {
  color: #fff;
}

.hero {
  padding-bottom: 80px;
}

.hero .hero__wrapper {
  flex-direction: column;
  gap: 16px;
  padding-bottom: 80px;
  display: flex;
}

.hero .hero__wrapper .case-title {
  margin-bottom: 16px;
}

@media (width <= 680px) {
  .contener {
    height: 90%;
  }
}

